import styles from "./FollowerList.module.scss";
import Button from "components/Button/Button";
import Avatar from "../../assets/icons/default-avatar.svg";
import { useState } from "react";
export function FollowerList({
  avatar,
  name,
  introduction,
  isFollowed,
  Follow,
  Unfollow,
  followerId,
}) {
  const [isFollower, setIsFollower] = useState(isFollowed);
  const token = localStorage.getItem("token");
  const handleClick = async () => {
    console.log("Clicked user ID:", followerId);
    if (isFollower) {
      setIsFollower(true);
      const success = await Unfollow(followerId, token);
      if (success) {
        setIsFollower(true);
      }
    } else {
      const success = await Follow(followerId, token);
      if (success) {
        setIsFollower(false);
      }
    }
  };
  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <div className={styles.top}>
          <div className={styles.avatar}>
            <img src={avatar || Avatar} alt="avatar" />
          </div>
          <div className={styles.followerName}>{name}</div>
          <div className={styles.button}>
            {isFollower ? (
              <Button
                title=""
                size="middle"
                onClick={() => handleClick(followerId)}
                isAction
              />
            ) : (
              <Button
                title=""
                size="small"
                onClick={() => handleClick(followerId)}
              />
            )}
          </div>
        </div>
        <div className={styles.introduction}>{introduction}</div>
      </div>
    </div>
  );
}
export function FollowingList({
  avatar,
  name,
  introduction,
  isFollowed,
  Follow,
  Unfollow,
  followingId,
}) {
  const [isFollower, setIsFollower] = useState(isFollowed);
  const handleClick = async () => {
    console.log("Clicked user ID:", followingId);
    if (isFollower) {
      // 
      setIsFollower(false);
      const success = await Unfollow(followingId); //  ID
      if (success) {
        setIsFollower(false);
      }
    } else {
      // 
      const success = await Follow(followingId); // ID
      setIsFollower(true);

      if (success) {
        setIsFollower(false);
      }
    }
  };
  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <div className={styles.top}>
          <div className={styles.avatar}>
            <img src={avatar || Avatar} alt="avatar" />
          </div>
          <div className={styles.followerName}>{name}</div>
          <div className={styles.button}>
            {isFollower ? (
              <Button
                title=""
                size="middle"
                onClick={() => handleClick(followingId)}
                isAction
              />
            ) : (
              <Button
                title=""
                size="small"
                onClick={() => handleClick(followingId)}
              />
            )}
          </div>
        </div>
        <div className={styles.introduction}>{introduction}</div>
      </div>
    </div>
  );
}
